section.contact {
  @media screen and (min-width: 1200px) {
    display: none;
  }
  padding: 0;
  @media screen and (max-width: 1199px) {
    background-color: #303030;
    background-image: linear-gradient(
        rgba(25, 28, 38, 0.913),
        rgba(3, 3, 3, 0.9)
      ),
      url(../images/contact/imgTell.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 208px 0 183px 31px;
    flex-grow: 1;

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: linear-gradient(
          rgba(31, 31, 57, 0.853),
          rgba(72, 72, 86, 0.602)
        ),
        url(../images/contact/imgTell.jpg);
    }

    @media screen and (min-width: 768px) {
      background-image: linear-gradient(
          rgba(25, 28, 38, 0.913),
          rgba(3, 3, 3, 0.9)
        ),
        url(../images/contact/imgTab.jpg);

      padding: 280px 70px 234px;

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: linear-gradient(
            rgba(25, 28, 38, 0.913),
            rgba(3, 3, 3, 0.9)
          ),
          url(../images/contact/imgTab.jpg);
      }
    }
  }
}

.container.contact {
  text-align: center;
}

.contact_logo {
  font-family: 'Raleway';
  font-style: normal;
  display: inline-block;
  text-decoration: none;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  color: white;
  margin-bottom: 20px;
  transition: var(--animation);
  &:hover {
    color: var(--orange-color);
  }
  &:hover .contact_logo--span {
    color: white;
  }
}

.contact_logo--span {
  transition: var(--animation);
  color: var(--orange-color);
}

.soc-list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.soc-item:not(:last-child) {
  margin-right: 20px;
}

.soc-link {
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white-color);
  border-radius: 50%;
  transition: background-color var(--animation), border-radius var(--animation);
  @media screen and (min-width: 1200px) {
    width: 50px;
    height: 50px;
  }

  &.master {
    color: var(--grey-color);
    margin-bottom: 10px;
  }

  &:hover,
  &:focus {
    background-color: var(--orange-color);
    border-radius: 50%;
  }
}

.footer-social-logo {
  fill: currentColor;

  @media screen and (min-width: 1200px) {
    width: 34px;
    height: 34px;
  }
}

.header_logo.footer {
  display: block;
  margin-bottom: 10px;
}

.tel_footer {
  display: inline-block;
  font-style: normal;
  text-decoration: none;
  font-size: 14px;
  line-height: 1.7;
  color: var(--white-color);
  transition: color var(--animation);

  @media screen and (min-width: 1200px) {
    font-size: 22px;
  }

  &:hover,
  &:focus {
    color: var(--orange-color);
  }
}
