.title {
  font-family: 'Raleway';
  font-size: 48px;
  line-height: 1.16;
  letter-spacing: 0.05em;
  color: var(--white-color);
  margin-bottom: 20px;

  @media screen and (min-width: 1200px) {
    font-size: 72px;
    line-height: 1.18;
    letter-spacing: 0.05em;
  }
  // -------------------------About------------------------
  &.about {
    width: 330px;
    font-size: 28px;
    line-height: 1.16;
    color: #303030;

    @media screen and (min-width: 1200px) {
      font-size: 42px;
      width: 483px;
    }
  }
  // -------------------------Price------------------------
  &.price {
    font-size: 28px;
    margin-bottom: 44px;

    @media screen and (min-width: 1200px) {
      font-size: 42px;
    }
  }
  // -------------------------School------------------------
  &.school {
    font-size: 36px;
    color: var(--black-color);
    width: 418px;

    @media screen and (min-width: 768px) {
      font-size: 42px;
    }
  }
  // --------------------------form-----------------------
  &.form {
    margin-bottom: 44px;
    text-align: center;
    font-size: 36px;
  }

  &.masters {
    font-size: 36px;
    margin-bottom: 44px;
    color: var(--black-color);
  }
}
